import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../PlateformUser/PlateformUser.module.css";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useAdmissionOngoingApplication } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import TransferSelectOngoingAppllicationCard from "./TransferSelectOngoingAppllicationCard";
const TransferSelectOngoingAppllication = ({
  onClose,
  onSelect,
  aid,
  selectedId,
  isList,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const { admissionOngoingApplication, admissionOngoingApplicationLoading } =
    useAdmissionOngoingApplication({
      data: {
        aid: aid,
        page: page,
        limit: 10,
        search: search,
      },
      skip: !aid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplication?.list?.length) {
      let obj = nextPage(admissionOngoingApplication?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplication?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("ongoing_applications")}</h6>
              </div>

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
                className={style.close_icon}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <div
              className={style.assign_search_container}
              style={{
                marginBottom: "1rem",
              }}
            >
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchEvent}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>

            {admissionOngoingApplication?.list?.map((application, index) =>
              admissionOngoingApplication?.list?.length === index + 1 ? (
                <div key={application?._id} ref={ref}>
                  <TransferSelectOngoingAppllicationCard
                    application={application}
                    onSelect={onSelect}
                    isList={isList}
                    selectedId={selectedId}
                  />
                </div>
              ) : (
                <TransferSelectOngoingAppllicationCard
                  application={application}
                  key={application?._id}
                  onSelect={onSelect}
                  isList={isList}
                  selectedId={selectedId}
                />
              )
            )}

            {admissionOngoingApplicationLoading ? (
              search ? (
                <QvipleLoader />
              ) : (
                <Loading />
              )
            ) : search ? (
              admissionOngoingApplication?.list?.length === 0 && (
                <EmptyMessage
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                  title="No ongoing applications found related this search."
                />
              )
            ) : (
              admissionOngoingApplication?.list?.length === 0 && (
                <EmptyMessage
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                  title="No ongoing applications list."
                />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default TransferSelectOngoingAppllication;
