import ModalMenu from "../../../Ui/Modal/ModalMenu";
import AddAdmissionModerator from "./AddAdmissionModerator";
import { useTranslation } from "react-i18next";
import { useState } from "react";
import {
  useAdmissionRemoveModerator,
  useAdmissionUpdateModerator,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";

const AdmissionModeratoMenu = ({
  onClose,
  aid,
  openModeratorMenu,
  instituteId,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [admissionRemoveModerator] = useAdmissionRemoveModerator();
  const [admissionUpdateModerator] = useAdmissionUpdateModerator();

  const onDelete = () => {
    if (aid && openModeratorMenu?._id) {
      setDisabled((pre) => !pre);
      admissionRemoveModerator({
        aid: aid,
        modId: openModeratorMenu?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  const onEdit = (val, application) => {
    if (openModeratorMenu?._id) {
      setDisabled((pre) => !pre);
      onOpenEdit();
      admissionUpdateModerator({
        modId: openModeratorMenu?._id,
        active_tab: val?.one_tab,
        updateModerator: {
          role: val?.mod_role,
          staffId: val?.sid,
          app_array:
            val?.mod_role === "MULTI_APP_ACCESS" ||
            val?.mod_role === "MULTI_APP_ACCESS"
              ? application
              : [],
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };
  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };

  return (
    <>
      {edit ? (
        <AddAdmissionModerator
          onClose={onOpenEdit}
          onEdit={onEdit}
          openModeratorMenu={openModeratorMenu}
          openAs="EDIT"
          aid={aid}
          instituteId={instituteId}
        />
      ) : (
        <ModalMenu onClose={onClose} disabled={disabled}>
          <h6 onClick={onOpenEdit}>{t("edit")}</h6>
          <h6 onClick={onDelete}>{t("delete")}</h6>
        </ModalMenu>
      )}
    </>
  );
};

export default AdmissionModeratoMenu;
