import { assestsUserAdmissionUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../OngoingAdmission/OngoingAdmission.module.css";

const TransferSelectOngoingAppllicationCard = ({
  application,
  onSelect,
  selectedId,
  isList,
}) => {
  const onClick = () => {
    onSelect(application);
  };
  return (
    <>
      {isList ? (
        <div
          className={
            selectedId?.includes(application?._id)
              ? `${style.ongoing_card} ${style.ongoing_card_active}`
              : style.ongoing_card
          }
          onClick={onClick}
        >
          <div className={style.ongoing_card_inner}>
            <img
              alt="Ongoing App"
              src={`${assestsUserAdmissionUrl}/application.svg`}
            />
            <div className={style.ongoing_card_text}>
              <h6>{application?.applicationName ?? ""}</h6>
              <p>{application?.applicationDepartment?.dName ?? ""}</p>
            </div>
          </div>
        </div>
      ) : (
        <div
          className={
            selectedId === application?._id
              ? `${style.ongoing_card} ${style.ongoing_card_active}`
              : style.ongoing_card
          }
          onClick={onClick}
        >
          <div className={style.ongoing_card_inner}>
            <img
              alt="Ongoing App"
              src={`${assestsUserAdmissionUrl}/application.svg`}
            />
            <div className={style.ongoing_card_text}>
              <h6>{application?.applicationName ?? ""}</h6>
              <p>{application?.applicationDepartment?.dName ?? ""}</p>
            </div>
          </div>
        </div>
      )}
    </>
  );
};

export default TransferSelectOngoingAppllicationCard;
