import { useEffect, useState } from "react";
import style from "../../FinanceManager/Moderator/FinanceModerator.module.css";
import PreviewAdmissionModerator from "./PreviewAdmissionModerator";
import { useTranslation } from "react-i18next";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Modal from "../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import CreateButton from "../../../Ui/Button/CreateButton";
import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../../Services/UrlConfig/BaseUrl";
import CustomSelect from "../../../Ui/Select/CustomSelect";
import InstituteStaff from "../../../UserStaffMember/InstituteStaff/InstituteStaff";
import { useAdmissionAddModerator } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import SetButton from "../../../Ui/Button/SetButton";
import dayjs from "dayjs";
import TransferSelectOngoingAppllication from "../OneOngoingAdmission/TransferApplication/TransferSelectOngoingAppllication";

const AddAdmissionModerator = ({
  instituteId,
  aid,
  onClose,
  openModeratorMenu,
  openAs,
  onEdit,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [activePopup, setActivePopup] = useState(
    openAs === "EDIT" ? "PREVIEW_FORM" : "OPEN_STAFF_LIST"
  );
  const [selectedStaff, setSelectedStaff] = useState(
    openAs === "EDIT" ? openModeratorMenu?.access_staff : ""
  );
  const [moderatorState, setModeratorState] = useState({
    mod_role: openAs === "EDIT" ? openModeratorMenu?.access_role : "",
    sid: openAs === "EDIT" ? openModeratorMenu?.access_staff?._id : "",
    app_array: [],
    one_tab: "",
  });
  const [selectedApplication, setSelectedApplication] = useState(
    openAs === "EDIT" ? openModeratorMenu?.access_application : []
  );
  const [selectedApplicationId, setSelectedApplicationId] = useState([]);

  const [admissionAddModerator] = useAdmissionAddModerator();

  useEffect(() => {
    if (openAs === "EDIT") {
      setSelectedStaff(openModeratorMenu?.access_staff);
      setSelectedApplication(openModeratorMenu?.access_application);
      let ids = [];
      for (let val of openModeratorMenu?.access_application) {
        ids.push(val?._id);
      }
      setSelectedApplicationId(ids);
      setModeratorState({
        mod_role: openModeratorMenu?.access_role,
        sid: openModeratorMenu?.access_staff?._id,
        app_array: ids,
      });
      setActivePopup("PREVIEW_FORM");
    }
  }, [openAs]);

  const onSelectStaff = (staff) => {
    setSelectedStaff(staff);
    setModeratorState((prev) => ({
      ...prev,
      sid: staff?._id,
    }));
    if (openAs === "EDIT") {
      setActivePopup("PREVIEW_FORM");
    } else {
      setActivePopup("ASSIGN_ROLE");
    }
  };

  // console.info("access_role", openModeratorMenu);

  const onSelectRole = (val) => {
    setModeratorState((prev) => ({
      ...prev,
      mod_role:
        val === "Fee receipt authority"
          ? "ALL_FEE_ACCESS"
          : val === "Application Manager"
          ? "MULTI_APP_ACCESS"
          : val === "Enquiries Section"
          ? "INQUIRY_ACCESS"
          : val === "Excess Fee Refund"
          ? "REFUND_ACCESS"
          : val === "Pending Fee Manager"
          ? "PENDING_FEE_ACCESS"
          : val === "Tab Authority"
          ? "ONE_TAB_ACCESS"
          : val === "Data Export Authority"
          ? "DATA_EXPORT_ACCESS"
          : "",
    }));
  };

  const onSelectTab = (val) => {
    setModeratorState((prev) => ({
      ...prev,
      one_tab: val,
    }));
  };

  const onAssignModerator = () => {
    setActivePopup("LOADING");
    admissionAddModerator({
      aid: aid,
      active_tab: moderatorState.one_tab,
      addModerator:
        moderatorState?.mod_role === "MULTI_APP_ACCESS" ||
        moderatorState?.mod_role === "ONE_TAB_ACCESS"
          ? {
              mod_role: moderatorState.mod_role,
              sid: moderatorState.sid,
              app_array: selectedApplicationId,
            }
          : moderatorState,
    })
      .then(() => {
        onRefetch();
        setActivePopup("");
        onClose();
      })
      .catch({});
  };

  const onUpdateModerator = () => {
    onEdit(moderatorState, selectedApplicationId);
    onClose();
  };
  const onCloseEdit = () => {
    onClose();
  };
  const onSelectApplication = (val) => {
    if (selectedApplicationId?.includes(val?._id)) {
      setSelectedApplicationId((prev) => prev?.filter((id) => id !== val?._id));
      setSelectedApplication((prev) =>
        prev?.filter((id) => id?._id !== val?._id)
      );
    } else {
      setSelectedApplicationId((prev) => [...prev, val?._id]);
      setSelectedApplication((prev) => [...prev, val]);
    }
    setActivePopup("MODERATOR_PREWVIEW");
  };
  return (
    <>
      {activePopup === "OPEN_STAFF_LIST" && (
        <InstituteStaff
          onSelectStaff={onSelectStaff}
          onClose={onClose}
          instituteId={instituteId}
        />
      )}
      {(activePopup === "ASSIGN_ROLE" ||
        activePopup === "MODERATOR_PREWVIEW") && (
        <>
          <Modal onClose={onClose}>
            <div className={style.modal_container}>
              <UserMemberContentWrapper>
                <div className={style.modal_container_header}>
                  <h6>{t("assign_role_to_moderator")}</h6>
                </div>
              </UserMemberContentWrapper>
              <BorderBottom />
              <UserMemberContentWrapper
                customStyle={{
                  paddingTop: "0",
                }}
              >
                <section className={style.selected_staff_container}>
                  <img
                    src={
                      selectedStaff?.staffProfilePhoto
                        ? `${imageShowUrl}/${selectedStaff?.staffProfilePhoto}`
                        : `${assestsFeedUrl}/feed-user-avatar.svg`
                    }
                    alt="staff profile avatar"
                  />
                  <div className={style.selected_staff_text}>
                    <h6>
                      {`${selectedStaff?.staffFirstName ?? ""} ${
                        selectedStaff?.staffMiddleName ?? ""
                      } ${selectedStaff?.staffLastName ?? ""}`}
                    </h6>
                    <p>
                      {t("index_no")}
                      {selectedStaff?.staffROLLNO ?? ""}
                    </p>
                  </div>
                </section>

                <CustomSelect
                  selectLabel={t("select_role_of_moderator")}
                  selectedValue={t("please_select_role")}
                  options={[
                    "Fee receipt authority",
                    "Application Manager",
                    "Enquiries Section",
                    // "Excess Fee Refund",
                    // "Pending Fee Manager",
                    "Tab Authority",
                    "Data Export Authority",
                  ]}
                  defalutValue={
                    moderatorState?.mod_role === "ALL_FEE_ACCESS"
                      ? "Fee receipt authority"
                      : moderatorState?.mod_role === "MULTI_APP_ACCESS"
                      ? "Application Manager"
                      : moderatorState?.mod_role === "INQUIRY_ACCESS"
                      ? "Enquiries Section"
                      : moderatorState?.mod_role === "REFUND_ACCESS"
                      ? "Excess Fee Refund"
                      : moderatorState?.mod_role === "PENDING_FEE_ACCESS"
                      ? "Pending Fee Manager"
                      : moderatorState?.mod_role === "ONE_TAB_ACCESS"
                      ? "Tab Authority"
                      : moderatorState?.mod_role === "DATA_EXPORT_ACCESS"
                      ? "Data Export Authority"
                      : ""
                  }
                  onClick={onSelectRole}
                />
                {moderatorState.mod_role === "ONE_TAB_ACCESS" && (
                  <CustomSelect
                    selectLabel={t("select_tab_role")}
                    selectedValue={t("please_select_role")}
                    options={[
                      "Application",
                      "Selected",
                      "Confirmed",
                      "Allotted",
                      "Cancelled",
                    ]}
                    defalutValue={moderatorState?.one_tab}
                    onClick={onSelectTab}
                  />
                )}

                {activePopup === "MODERATOR_PREWVIEW" &&
                  selectedApplication?.map((app) => (
                    <div className={style.application_card} key={app?._id}>
                      <img
                        src={`${assestsUserAdmissionUrl}/application.svg`}
                        alt="application icon"
                      />
                      <section className={style.application_card_text}>
                        <h6>{app?.applicationName ?? ""}</h6>
                        <div className={style.application_card_text_bottom}>
                          <p>{app?.applicationDepartment?.dName ?? ""}</p>
                          {app?.applicationEndDate ? (
                            <p>
                              {dayjs(app?.applicationEndDate).format(
                                "DD/MM/YYYY"
                              )}
                            </p>
                          ) : (
                            ""
                          )}
                        </div>
                      </section>
                    </div>
                  ))}
                {moderatorState.mod_role === "MULTI_APP_ACCESS" ? (
                  <>
                    {activePopup === "MODERATOR_PREWVIEW" ? (
                      <>
                        <SetButton
                          label="add_more"
                          onAction={() => setActivePopup("SELECT_APPLICATION")}
                        />
                        <CreateButton
                          label="assign"
                          onAction={onAssignModerator}
                        />
                      </>
                    ) : (
                      <SetButton
                        label="moderator_select_application"
                        onAction={() => setActivePopup("SELECT_APPLICATION")}
                      />
                    )}
                  </>
                ) : (
                  <CreateButton label="assign" onAction={onAssignModerator} />
                )}
              </UserMemberContentWrapper>
              {activePopup === "LOADING" && <QvipleLoader />}
            </div>
          </Modal>
        </>
      )}

      {activePopup === "PREVIEW_FORM" && (
        <PreviewAdmissionModerator
          onCloseEdit={onCloseEdit}
          setActivePopup={setActivePopup}
          selectedStaff={selectedStaff}
          moderatorState={moderatorState}
          onSelectRole={onSelectRole}
          onUpdateModerator={onUpdateModerator}
          selectedApplication={selectedApplication}
        />
      )}
      {activePopup === "SELECT_APPLICATION" && (
        <TransferSelectOngoingAppllication
          aid={aid}
          onClose={onCloseEdit}
          onSelect={onSelectApplication}
          isList
          selectedId={selectedApplicationId}
        />
      )}
      {activePopup === "LOADING" && <QvipleLoader />}
    </>
  );
};

export default AddAdmissionModerator;
